body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img[src=""] {
  display: none;
}
.actor {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.divisions {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-items: center;
  border: 2px solid black;
  border-radius: 1rem;
  margin: 1rem;
}
.divisions > h4 {
  font-size: 1.2rem;
  margin: 0.5rem;
  text-align: center;
}
.divisions > img {
  border-radius: 1rem;
  margin: 1rem;
}

.clip {
  width: 250px;
  height: 300px;
  margin: 50px;
  border-radius: 10px;
  margin-bottom: 70px;
  display: block;

  align-items: center;
}
.img {
  top: 0;
  width: 200px;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
}
p {
  width: 250px;
  font-size: 1.2rem;
  text-align: center;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2) {
  .clip {
    width: 150px;
    height: 260px;
    margin: 20px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
  }
  .img {
    top: 0;
    width: 150px;
    height: 220px;
    object-fit: cover;
    border-radius: 10px;
  }
  p {
    width: 170px;
    font-size: 0.87rem;
    font-weight: 500;
    text-align: center;
  }
}

