  }

  input {
    font-size: 1rem;
    width: 18rem;
    height: 2rem;
    border: 2px solid black;
    border-radius: 30px;
    text-align: center;
    align-content: center;
    color: black;
    outline: none;
    margin-bottom: 0.5rem;
  }
  .btn-grad {
    margin: 10px;
    font-size: 14px;
    padding: 4px 20px;
    text-align: center;
    outline: none;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;
    display: block;
  }
  .nodata-img {
    width: 20rem;
    height: 13rem;
  }
  /* .loading {
    width: 6rem;
    height: 6rem;
  } */
  .Modal2 {
    width: 340px;
    height: 500px;
    background-color: #f5f0f0;
    background-image: radial-gradient(
      circle farthest-corner at 5.6% 54.5%,
      rgba(47, 71, 79, 1) 0%,
      rgba(159, 188, 198, 1) 83.6%
    );

    overflow: auto;
    display: flex;
    flex-direction: column;

    overflow: auto;
  }
  .Modal2 > div > img {
    margin-top: 7%;
    margin-left: 9%;
    margin-bottom: 40px;
    border-radius: 10px;
  }
  .Modal2 > img {
    width: 4rem;
    height: 4rem;
    margin-top: 60%;
    margin-left: 40%;
  }
  .Modal2 > h3 {
    margin-right: 10px;
    margin-left: 20px;
    margin-bottom: 15px;
    font-size: 1rem;
  }
}

